// src/pages/Lists/AddContactList.tsx

import { useRef, useState } from "react";
import { useForm, useFieldArray, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  SheetClose,
  SheetTitle,
  SheetDescription,
} from "@/components/ui/sheet";
import {
  UploadIcon,
  Trash2,
  PlusIcon,
  ListFilter,
  Users,
  Mail,
  FileText,
  Info,
} from "lucide-react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import Papa from "papaparse";
import * as XLSX from "xlsx";
import { jwtDecode } from "jwt-decode";
import { createContactService, createList, addContactToList } from "@/api/services/contact/contactService";
import secureLocalStorage from "react-secure-storage";
import { toast } from "sonner";
import { Textarea } from "@/components/ui/textarea";
import axiosInstance from "@/api/axios/axiosInstance";
import { useAuth } from "@/context/AuthContext";
import { usePermissions } from "@/context/PermissionContext";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";

// Define the shape of a single contact
interface Contact {
  fullName: string;
  email: string;
  phoneNumber: string;
}

// Define the shape of form data
interface FormData {
  listName: string;
  description: string;
  type: string;
  contacts: Contact[];
}

// Define the decoded token structure (adjust based on your actual token)
interface DecodedToken {
  user: {
    user_id?: number;
    id?: number;
  };
}

const validationSchema = Yup.object().shape({
  listName: Yup.string()
    .required("List Name is required")
    .min(2, "List Name must be at least 2 characters"),
  description: Yup.string().required("Description is required"),
  type: Yup.string().required("Type is required"),
  contacts: Yup.array()
    .of(
      Yup.object().shape({
        fullName: Yup.string()
          .required("Full Name is required")
          .min(2, "Full Name must be at least 2 characters"),
        email: Yup.string()
          .required("Email is required")
          .email("Email is invalid"),
        phoneNumber: Yup.string()
          .required("Phone Number is required")
          .min(10, "Phone Number must be at least 10 digits"),
      })
    )
    .required("Contacts are required") // Ensure contacts is required
    .min(1, "At least one contact is required"),
});

export function AddContactList() {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [showInsufficientCreditsModal, setShowInsufficientCreditsModal] = useState(false);
  const [currentBalance, setCurrentBalance] = useState(0);

  const { user } = useAuth();
  const { isSuperAdmin } = usePermissions();

  // Initialize React Hook Form
  const {
    register,
    control,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset,
    watch,
    setValue,
  } = useForm<FormData>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      listName: "",
      description: "",
      type: "General",
      contacts: [{ fullName: "", email: "", phoneNumber: "" }],
    },
  });

  // Initialize useFieldArray for dynamic contacts
  const { fields, append, remove } = useFieldArray({
    control,
    name: "contacts",
  });

  // Retrieve and decode the token
  const getUserId = (): number | null => {
    const token = secureLocalStorage.getItem("token");
    if (!token) {
      console.log("No token found in storage");
      return null;
    }

    try {
      const decoded: any = jwtDecode(token as string);
      console.log("Decoded token:", decoded);

      // Handle different token structures:
      // 1. Regular login: decoded.user.id or decoded.user.user_id
      // 2. Demo login: decoded.id
      // 3. Legacy: decoded.user_id
      let userId = null;

      if (decoded.user?.id) {
        userId = decoded.user.id;
      } else if (decoded.user?.user_id) {
        userId = decoded.user.user_id;
      } else if (decoded.id) {
        userId = decoded.id;
      } else if (decoded.user_id) {
        userId = decoded.user_id;
      }

      console.log("Extracted userId:", userId);
      return userId;
    } catch (error) {
      console.error("Error decoding token:", error);
      return null;
    }
  };

  // Handle form submission
  const onSubmit = async (data: FormData) => {
    // Check credits first (unless super admin)
    if (!isSuperAdmin) {
      try {
        const response = await axiosInstance.get('/api/credits/balance');
        const balance = response.data.data;

        if (!balance || parseFloat(balance.available_credits) < 1) {
          setCurrentBalance(balance?.available_credits || 0);
          setShowInsufficientCreditsModal(true);
          return;
        }
      } catch (error) {
        console.error('Error checking credit balance:', error);
        toast.error('Unable to verify credit balance. Please try again.');
        return;
      }
    }

    const userId = getUserId();
    if (!userId) {
      toast.error("User ID not found. Please log in again.");
      return;
    }

    try {
      // First create the list using the new API
      const listResponse = await createList({
        listName: data.listName,
        description: data.description,
        type: data.type
      });

      // If list creation is successful, use the legacy API for contacts
      // (until we fully migrate to the new structure)
      const formattedContacts = data.contacts.map((contact) => ({
        email: contact.email,
        contact_number: contact.phoneNumber,
        first_name: contact.fullName, // Map fullName to first_name for legacy compatibility
      }));

      const payload = {
        user_id: userId,
        list_name: data.listName,
        list_description: data.description,
        contacts: formattedContacts,
      };

      await createContactService(payload);

      reset({
        listName: "",
        description: "",
        type: "General",
        contacts: [{ fullName: "", email: "", phoneNumber: "" }],
      });

      toast.success("Contact List created successfully!");

      // Optionally refresh the parent component
      if (typeof window !== 'undefined') {
        window.location.reload();
      }
    } catch (error) {
      console.error("Error submitting form:", error);
      toast.error("Failed to create contact list. Please try again.");
    }
  };

  // Handle file import click
  const handleImportClick = () => {
    fileInputRef.current?.click();
  };

  // Handle file selection and parsing
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();

      reader.onload = (evt) => {
        const data = evt.target?.result;
        if (data) {
          const fileExtension = file.name.split(".").pop()?.toLowerCase();

          if (fileExtension === "csv") {
            // Existing CSV parsing code
          } else if (fileExtension === "xlsx") {
            const workbook = XLSX.read(data, { type: "binary" });
            const sheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[sheetName];
            const jsonData = XLSX.utils.sheet_to_json<Contact>(worksheet, {
              defval: "",
            });

            // Log the raw JSON data
            console.log("Excel JSON Data:", jsonData);

            const validContacts = jsonData
              .map((contact: any) => ({
                fullName:
                  contact.fullName ||
                  contact["Full Name"] ||
                  contact["Name"] ||
                  contact.firstName ||
                  contact["First Name"] ||
                  "",
                email:
                  contact.email ||
                  contact["Email"] ||
                  contact["Email Address"] ||
                  "",
                phoneNumber:
                  contact.phoneNumber ||
                  contact["Phone Number"] ||
                  contact["Contact Number"] ||
                  contact.contactNumber ||
                  "",
              }))
              .filter((contact) => contact.email && contact.phoneNumber);

            validContacts.forEach((contact) => {
              append({
                fullName: contact.fullName,
                email: contact.email,
                phoneNumber: contact.phoneNumber,
              });
            });

            toast.success("Contacts imported successfully!");
            console.log("Parsed Contacts:", validContacts);
          } else {
            toast.error(
              "Unsupported file format. Please upload a CSV or Excel file."
            );
          }
        }
      };

      if (
        file.type ===
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
        file.type === "application/vnd.ms-excel" ||
        file.type === "text/csv"
      ) {
        reader.readAsBinaryString(file);
      } else {
        toast.error("Please upload a valid CSV or Excel file.");
      }

      // Reset the input value to allow re-uploading the same file if needed
      e.target.value = "";
    }
  };

  return (
    <>
      <SheetTitle className="text-xl font-semibold jakarta text-white mb-1">
        Create Contact List
      </SheetTitle>
      <SheetDescription className="text-white/60 mb-6">
        Create a new contact list for your voice AI agents to use for campaigns.
      </SheetDescription>

        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <div className="space-y-4 bg-white/5 border border-white/10 rounded-lg p-4">
          <div className="flex items-center space-x-2 mb-2">
            <ListFilter className="h-5 w-5 text-indigo-400" />
            <h3 className="text-sm font-medium text-white">List Information</h3>
          </div>

          {/* List Name Field */}
          <div>
            <Label
              htmlFor="listName"
              className="text-sm font-medium text-white mb-1.5 block"
            >
              List Name<span className="text-red-500">*</span>
            </Label>
            <Input
              id="listName"
              placeholder="Enter contact list name"
              className="w-full bg-white/5 border-white/20 text-white placeholder:text-white/40 focus:border-indigo-500/50"
              {...register("listName")}
            />
            {errors.listName && (
              <p className="mt-1 text-xs text-red-400">
                {errors.listName.message}
              </p>
            )}
          </div>

          {/* Description Field */}
          <div>
            <Label
              htmlFor="description"
              className="text-sm font-medium text-white mb-1.5 block"
            >
              Description<span className="text-red-500">*</span>
            </Label>
            <Textarea
              id="description"
              placeholder="Enter contact list description"
              className="w-full min-h-[80px] bg-white/5 border-white/20 text-white placeholder:text-white/40 focus:border-indigo-500/50"
              {...register("description")}
            />
            {errors.description && (
              <p className="mt-1 text-xs text-red-400">
                {errors.description.message}
              </p>
            )}
          </div>

          {/* Type Field */}
          <div>
            <Label
              htmlFor="type"
              className="text-sm font-medium text-white mb-1.5 block"
            >
              Type<span className="text-red-500">*</span>
            </Label>
            <Select
              value={watch("type")}
              onValueChange={(value) => setValue("type", value)}
            >
              <SelectTrigger className="w-full bg-white/5 border-white/20 text-white focus:border-indigo-500/50">
                <SelectValue placeholder="Select list type" />
              </SelectTrigger>
              <SelectContent className="bg-[#0e1340] border-white/10">
                <SelectItem value="General" className="text-white hover:bg-white/10 focus:bg-white/10">General</SelectItem>
                <SelectItem value="Marketing" className="text-white hover:bg-white/10 focus:bg-white/10">Marketing</SelectItem>
                <SelectItem value="Sales" className="text-white hover:bg-white/10 focus:bg-white/10">Sales</SelectItem>
                <SelectItem value="Event" className="text-white hover:bg-white/10 focus:bg-white/10">Event</SelectItem>
                <SelectItem value="Customer" className="text-white hover:bg-white/10 focus:bg-white/10">Customer</SelectItem>
              </SelectContent>
            </Select>
            {errors.type && (
              <p className="mt-1 text-xs text-red-400">
                {errors.type.message}
              </p>
            )}
          </div>
        </div>

        <div className="space-y-4">
          <div className="flex items-center space-x-2 mb-2">
            <Users className="h-5 w-5 text-indigo-400" />
            <h3 className="text-sm font-medium text-white">Contacts</h3>
            <span className="text-xs text-red-500">*</span>
          </div>

          {/* Contacts Section */}
          <div className="space-y-4">
            {fields.map((field, index) => (
              <div
                key={field.id}
                className="border border-indigo-500/30 rounded-lg p-4 mb-4 relative bg-indigo-500/10"
              >
                {/* Remove Contact Button */}
                {fields.length > 1 && (
                  <button
                    type="button"
                    onClick={() => remove(index)}
                    className="absolute top-3 right-3 text-white/40 hover:text-red-400 transition-colors"
                    title="Remove Contact"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                )}

                <div className="flex items-center space-x-2 mb-3">
                  <div className="h-6 w-6 bg-indigo-500/20 rounded-full flex items-center justify-center">
                    <span className="text-xs font-medium text-indigo-400">
                      {index + 1}
                    </span>
                  </div>
                  <h4 className="text-sm font-medium text-white">Contact {index + 1}</h4>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  {/* Full Name Field */}
                  <div>
                    <Label
                      htmlFor={`contacts.${index}.fullName`}
                      className="text-sm font-medium text-white mb-1.5 block"
                    >
                      Full Name<span className="text-red-500">*</span>
                    </Label>
                    <div className="relative">
                      <Input
                        id={`contacts.${index}.fullName`}
                        type="text"
                        placeholder="Enter full name"
                        className="w-full bg-white/5 border-white/20 text-white placeholder:text-white/40 focus:border-indigo-500/50"
                        {...register(`contacts.${index}.fullName` as const)}
                      />
                    </div>
                    {errors.contacts?.[index]?.fullName && (
                      <p className="mt-1 text-xs text-red-400">
                        {errors.contacts[index].fullName?.message}
                      </p>
                    )}
                  </div>

                  {/* Email Field */}
                  <div>
                    <Label
                      htmlFor={`contacts.${index}.email`}
                      className="text-sm font-medium text-white mb-1.5 block"
                    >
                      Email<span className="text-red-500">*</span>
                    </Label>
                    <div className="relative">
                      <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-white/40" />
                      <Input
                        id={`contacts.${index}.email`}
                        type="email"
                        placeholder="Enter contact email"
                        className="w-full pl-10 bg-white/5 border-white/20 text-white placeholder:text-white/40 focus:border-indigo-500/50"
                        {...register(`contacts.${index}.email` as const)}
                      />
                    </div>
                    {errors.contacts?.[index]?.email && (
                      <p className="mt-1 text-xs text-red-400">
                        {errors.contacts[index].email?.message}
                      </p>
                    )}
                  </div>

                  {/* Phone Number Field */}
                  <div>
                    <Label
                      htmlFor={`contacts.${index}.phoneNumber`}
                      className="text-sm font-medium text-white mb-1.5 block"
                    >
                      Phone Number<span className="text-red-500">*</span>
                    </Label>
                    <Controller
                      name={`contacts.${index}.phoneNumber` as const}
                      control={control}
                      render={({ field: { onChange, value } }) => (
                        <PhoneInput
                          country="us"
                          value={value}
                          onChange={(phone) => onChange(`+${phone}`)}
                          containerClass="w-full neo-phone-container"
                          inputClass="neo-phone-input"
                          buttonClass="neo-phone-flag"
                          dropdownClass="neo-phone-dropdown"
                          searchClass="neo-phone-search"
                          inputProps={{
                            name: `contacts.${index}.phoneNumber`,
                            required: true,
                            autoFocus: false,
                          }}
                          placeholder="Enter phone number"
                          enableSearch={true}
                          disableSearchIcon={true}
                        />
                      )}
                    />
                    {errors.contacts?.[index]?.phoneNumber && (
                      <p className="mt-1 text-xs text-red-400">
                        {errors.contacts[index].phoneNumber?.message}
                      </p>
                    )}
                  </div>
                </div>
              </div>
            ))}

            {/* Add Contact Button */}
            <button
              type="button"
              
              className="w-full bg-white/5 border-dashed border-white/20 text-white hover:bg-white/10 flex items-center justify-center gap-2 py-6"
              onClick={() => append({ fullName: "", email: "", phoneNumber: "" })}
            >
              <PlusIcon className="w-4 h-4" />
              Add Another Contact
            </button>
          </div>
        </div>

        {/* <div className="space-y-4">
          <div className="flex items-center space-x-2 mb-2">
            <UploadIcon className="h-5 w-5 text-primary" />
            <h3 className="text-sm font-medium">Bulk Import</h3>
          </div>

          
          <div className="border-2 border-dashed border-border/50 rounded-lg p-6 text-center">
            <div className="flex flex-col items-center justify-center">
              <UploadIcon className="h-8 w-8 text-muted-foreground mb-2" />
              <h3 className="text-sm font-medium mb-1">Import Contacts</h3>
              <p className="text-xs text-muted-foreground mb-3">
                Drag and drop a CSV or Excel file here or click to browse
              </p>
              <Button
                type="button"
                variant="outline"
                className="text-xs border-border/50 hover:bg-muted/20"
                onClick={handleImportClick}
              >
                Browse Files
              </Button>
              <input
                type="file"
                id="import"
                accept=".xlsx, .csv"
                ref={fileInputRef}
                onChange={handleFileChange}
                className="hidden"
              />
            </div>
          </div>

          <div className="flex items-center p-3 bg-muted/20 rounded-lg border border-border/50">
            <Info className="h-4 w-4 text-muted-foreground mr-2 flex-shrink-0" />
            <p className="text-xs text-muted-foreground">
              Your CSV or Excel file should have columns for 'Full Name', 'Email' and
              'Phone Number' (with country code)
            </p>
          </div>
        </div> */}

        {/* Action Buttons */}
        <div className="mt-8 flex justify-end space-x-3">
          <SheetClose asChild>
            <Button
              type="button"
              variant="outline"
              className="border-white/20 text-white hover:bg-white/10"
            >
              Cancel
            </Button>
          </SheetClose>

          <Button
            type="submit"
            className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 text-white"
            disabled={isSubmitting}
          >
            {isSubmitting ? "Creating..." : "Create List"}
          </Button>
        </div>
        </form>

        {/* Insufficient Credits Modal */}
        <Dialog open={showInsufficientCreditsModal} onOpenChange={setShowInsufficientCreditsModal}>
          <DialogContent className="sm:max-w-md bg-[#0e1340] border-white/10">
            <DialogHeader>
              <DialogTitle className="flex items-center gap-2">
                <div className="w-8 h-8 rounded-full bg-red-500/20 flex items-center justify-center">
                  <svg className="w-4 h-4 text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
                  </svg>
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-white">Insufficient Credits</h3>
                  <p className="text-sm text-white/60 font-normal">You need credits to create contact lists</p>
                </div>
              </DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              <div className="bg-white/5 p-4 rounded-lg border border-white/10">
                <div className="flex justify-between items-center">
                  <span className="text-sm text-white/60">Current Balance:</span>
                  <span className="font-semibold text-white">{currentBalance} credits</span>
                </div>
                <div className="flex justify-between items-center mt-2">
                  <span className="text-sm text-white/60">Required:</span>
                  <span className="font-semibold text-red-400">1 credit minimum</span>
                </div>
              </div>
              <div className="flex gap-3">
                <Button
                  variant="outline"
                  onClick={() => setShowInsufficientCreditsModal(false)}
                  className="flex-1 border-white/20 text-white hover:bg-white/10"
                >
                  Cancel
                </Button>
                <Button
                  onClick={() => {
                    setShowInsufficientCreditsModal(false);
                    window.location.href = '/credits';
                  }}
                  className="flex-1 bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-700 hover:to-purple-700 text-white"
                >
                  Buy Credits
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
    </>
  );
}
