import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

export interface ContactGmailStatus {
  isConnected: boolean;
  email: string | null;
  messagesTotal?: number;
  threadsTotal?: number;
}

export interface ContactEmailData {
  to: string;
  cc?: string;
  bcc?: string;
  subject: string;
  body: string;
  isHtml?: boolean;
}

// Get Contact Gmail connection status
export async function getContactGmailStatus(): Promise<ContactGmailStatus> {
  try {
    const res = await axiosInstance.get("/api/contact-gmail/auth/status");
    
    if (res.data.success) {
      return res.data.data;
    }
    
    throw new Error(res.data.message || "Failed to get Contact Gmail status");
  } catch (error: any) {
    console.error("Error getting Contact Gmail status:", error);
    return {
      isConnected: false,
      email: null
    };
  }
}

// Connect Contact Gmail (opens in new tab)
export async function connectContactGmail(): Promise<void> {
  try {
    const response = await axiosInstance.get("/api/contact-gmail/auth/url");

    if (response.data.success) {
      // Open Gmail OAuth in a new tab
      const newTab = window.open(
        response.data.data.authUrl,
        'contact_gmail_oauth',
        'width=500,height=600,scrollbars=yes,resizable=yes'
      );

      if (!newTab) {
        toast.error('Popup blocked. Please allow popups and try again.');
        return;
      }

      // Listen for messages from the OAuth callback
      return new Promise((resolve, reject) => {
        let resolved = false;

        const messageHandler = (event: MessageEvent) => {
          console.log('ðŸ“¨ Received postMessage:', event.data, 'from origin:', event.origin);

          // Only process Contact Gmail messages
          if (!event.data || !event.data.type || !event.data.type.startsWith('CONTACT_GMAIL_')) {
            console.log('âŒ Ignoring non-Contact Gmail message');
            return;
          }

          if (resolved) return; // Prevent multiple resolutions

          if (event.data.type === 'CONTACT_GMAIL_SUCCESS') {
            console.log('âœ… Contact Gmail success received');
            resolved = true;
            window.removeEventListener('message', messageHandler);
            toast.success(`Contact Gmail connected successfully! (${event.data.email})`);
            resolve();
          } else if (event.data.type === 'CONTACT_GMAIL_ERROR') {
            console.log('âŒ Contact Gmail error received:', event.data.error);
            resolved = true;
            window.removeEventListener('message', messageHandler);
            toast.error(`Failed to connect Contact Gmail: ${event.data.error}`);
            reject(new Error(event.data.error));
          }
        };

        window.addEventListener('message', messageHandler);

        // Check if tab was closed without completing OAuth
        const checkClosed = setInterval(() => {
          if (newTab.closed) {
            clearInterval(checkClosed);
            console.log('ðŸ”’ OAuth tab was closed');

            if (resolved) return; // Already handled by message

            window.removeEventListener('message', messageHandler);

            // Give more time for any pending messages, then check status
            setTimeout(async () => {
              if (resolved) return;

              console.log('ðŸ” Checking Contact Gmail status after tab close...');
              try {
                const status = await getContactGmailStatus();
                if (status.isConnected) {
                  console.log('âœ… Contact Gmail is connected despite no postMessage');
                  resolved = true;
                  toast.success("Contact Gmail connected successfully!");
                  resolve();
                } else {
                  console.log('âŒ Contact Gmail is not connected');
                  resolved = true;
                  reject(new Error("Contact Gmail connection was cancelled"));
                }
              } catch (error) {
                console.log('âŒ Error checking Contact Gmail status:', error);
                resolved = true;
                reject(new Error("Contact Gmail connection was cancelled"));
              }
            }, 2000); // Increased delay to 2 seconds
          }
        }, 1000);

        // Timeout after 5 minutes
        setTimeout(() => {
          if (resolved) return;

          clearInterval(checkClosed);
          window.removeEventListener('message', messageHandler);
          if (!newTab.closed) {
            newTab.close();
          }
          resolved = true;
          reject(new Error("Contact Gmail connection timeout"));
        }, 300000);
      });
    } else {
      throw new Error(response.data.message || "Failed to generate Contact Gmail auth URL");
    }
  } catch (error: any) {
    console.error("Error connecting Contact Gmail:", error);
    const message = error.response?.data?.message || "Failed to connect Contact Gmail";
    toast.error(message);
    throw error;
  }
}

// Disconnect Contact Gmail
export async function disconnectContactGmail(): Promise<void> {
  try {
    const res = await axiosInstance.delete("/api/contact-gmail/auth/disconnect");

    if (res.data.success) {
      toast.success("Contact Gmail disconnected successfully!");
      return;
    }

    throw new Error(res.data.message || "Failed to disconnect Contact Gmail");
  } catch (error: any) {
    console.error("Error disconnecting Contact Gmail:", error);
    const message = error.response?.data?.message || "Failed to disconnect Contact Gmail";
    toast.error(message);
    throw error;
  }
}

// Send email using Contact Gmail
export async function sendContactGmailEmail(emailData: ContactEmailData) {
  try {
    const res = await axiosInstance.post("/api/contact-gmail/send", emailData);

    if (res.data.success) {
      toast.success("Email sent successfully via Contact Gmail!");
      return res.data.data;
    }

    throw new Error(res.data.message || "Failed to send email via Contact Gmail");
  } catch (error: any) {
    console.error("Error sending Contact Gmail email:", error);
    const message = error.response?.data?.message || "Failed to send email via Contact Gmail";
    toast.error(message);
    throw error;
  }
}

// Check if Contact Gmail service is available
export async function isContactGmailServiceAvailable(): Promise<boolean> {
  try {
    const status = await getContactGmailStatus();
    return status.isConnected;
  } catch (error) {
    console.error("Error checking Contact Gmail service availability:", error);
    return false;
  }
}
