import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Email service using SMTP (sends from logged-in user's email)
export interface EmailData {
  to: string;
  cc?: string;
  bcc?: string;
  subject: string;
  body: string;
  isHtml?: boolean;
}

export interface EmailServiceStatus {
  isConfigured: boolean;
  transporters: number;
  testMode: boolean;
}

// Send email via SMTP service
export async function sendEmail(emailData: EmailData) {
  try {
    const res = await axiosInstance.post("/api/email/send", emailData);

    if (res.data.success) {
      const data = res.data.data;
      if (data.testMode) {
        toast.success("Email logged successfully (Test Mode)");
      } else {
        toast.success(`Email sent successfully via ${data.provider}!`);
      }
      return res.data.data;
    }

    throw new Error(res.data.message || "Failed to send email");
  } catch (error: any) {
    console.error("Error sending email:", error);
    const message = error.response?.data?.message || "Failed to send email";
    toast.error(message);
    throw error;
  }
}

// Send bulk emails via SMTP service
export async function sendBulkEmails(emails: EmailData[]) {
  try {
    const res = await axiosInstance.post("/api/email/send-bulk", { emails });

    if (res.data.success) {
      const results = res.data.data;
      const successCount = results.filter((r: any) => r.success).length;
      toast.success(`${successCount} emails sent successfully!`);
      return res.data.data;
    }

    throw new Error(res.data.message || "Failed to send bulk emails");
  } catch (error: any) {
    console.error("Error sending bulk emails:", error);
    const message = error.response?.data?.message || "Failed to send bulk emails";
    toast.error(message);
    throw error;
  }
}

// Get email service status
export async function getEmailServiceStatus(): Promise<EmailServiceStatus> {
  try {
    const res = await axiosInstance.get("/api/email/status");
    
    if (res.data.success) {
      return res.data.data;
    }
    
    throw new Error(res.data.message || "Failed to get email service status");
  } catch (error: any) {
    console.error("Error getting email service status:", error);
    const message = error.response?.data?.message || "Failed to get email service status";
   // toast.error(message);
    throw error;
  }
}

// Check if email service is available (either SMTP or Gmail)
export async function isEmailServiceAvailable(): Promise<boolean> {
  try {
    // Check SMTP service first
    const smtpStatus = await getEmailServiceStatus();
    if (smtpStatus.isConfigured) {
      return true;
    }

    // If SMTP is not configured, check Gmail
    const gmailResponse = await axiosInstance.get("/api/gmail/auth/status");
    return gmailResponse.data.success && gmailResponse.data.data.isConnected;
  } catch (error) {
    console.error("Error checking email service availability:", error);
    return false;
  }
}
