import { useState, useEffect } from "react";
import { ModernTable } from "@/components/ModernTable";
import Header from "@/components/Header";
import Page from "@/components/Page";
import { getAllContactLists, getContactsForList, deleteContactList, restoreContactList, updateContact, removeContactFromList, addContactToList } from "@/api/services/contact/contactService";
import { createOutboundCall, endCallViaControl, muteCall, unmuteCall } from "@/api/services/outbound/outboundService";
import { fetchAllAgentsComplete } from "@/api/services/agents/agentService";
import { getAllPhoneNumbers } from "@/api/services/phoneNumbers/phoneNumberService";
import {
  connectContactGmail,
  disconnectContactGmail,
  getContactGmailStatus,
  sendContactGmailEmail
} from "@/api/services/contactGmail/contactGmailService";
import { sendEmail as sendSMTPEmail, getEmailServiceStatus } from "@/api/services/email/emailService";
import {
  getContactCallHistory,
  getCallRecording,
  getCallTranscript,
  formatDuration,
  formatCallStatus,
  formatCallCost,
  formatCallDate,
  hasRecording,
  type CallHistoryItem,
  type CallHistoryResponse
} from "@/api/services/callHistory/callHistoryService";
import { useNavigate } from "react-router-dom";
import {
  Edit,
  Eye,
  Trash,
  Plus,
  Users,
  Calendar,
  ListFilter,
  Mail,
  UserPlus,
  ListChecks,
  Share2,
  MoreHorizontal,
  RotateCcw,
  AlertCircle,
  Search,
  Phone,
  Building,
  Tag,
  MessageSquare,
  History,
  FileText,
  X,
  User,
  Loader2,
  Mic,
  MicOff,
  Pause,
  Play,
  PhoneOff,
  Clock,
  Send,
  Paperclip,
} from "lucide-react";
import { AddContactList } from "./AddContactList";
import { Badge } from "@/components/ui/badge";
import { useSearchParams } from "react-router-dom";
import ListGrid from "@/components/ListGrid";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import ListFilterPopover from "@/components/ListFilterPopover";
import CustomPagination from "@/components/CustomPagination";
import { usePermissions } from "@/hooks/usePermissions";
import { toast } from "sonner";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import { Sheet, SheetContent } from "@/components/ui/sheet";
import axiosInstance from "@/api/axios/axiosInstance";
import { useAuth } from "@/context/AuthContext";
import { CallRecordingPlayer } from "@/components/CallRecordingPlayer";
import ChatSidebar from "@/components/ContactMessaging/ChatSidebar";

interface Contact {
  id: number;
  email: string;
  phoneNumber: string;
  fullName: string;
  createdAt: string;
  listId?: number;
  listName?: string;
  description?: string;
  list_type?: string;
}

interface ContactList {
  id: number;
  list_name: string;
  description: string;
  type: string;
  contactCount: number;
  createdAt: string;
  is_deleted?: boolean;
  deleted_at?: string;
}

const Lists = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const { isSuperAdmin } = usePermissions();
  const { user } = useAuth();

  // Main state
  const [lists, setLists] = useState<ContactList[]>([]);
  const [selectedList, setSelectedList] = useState<ContactList | null>(null);
  const [contacts, setContacts] = useState<Contact[]>([]);
  const [selectedContact, setSelectedContact] = useState<Contact | null>(null);
  const [loading, setLoading] = useState(true);
  const [contactsLoading, setContactsLoading] = useState(false);

  // Search states
  const [listSearch, setListSearch] = useState("");
  const [contactSearch, setContactSearch] = useState("");

  // View modes
  const [contactsView, setContactsView] = useState<"list" | "grid">("list");
  const [groupsView, setGroupsView] = useState<"list" | "grid">("list");

  // Pagination
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);

  // Modal states
  const [showAddListSheet, setShowAddListSheet] = useState(false);
  const [showInsufficientCreditsModal, setShowInsufficientCreditsModal] = useState(false);
  const [currentBalance, setCurrentBalance] = useState(0);

  // Contact editing states
  const [isEditingContact, setIsEditingContact] = useState(false);
  const [editingContactData, setEditingContactData] = useState({
    fullName: "",
    email: "",
    phoneNumber: ""
  });

  // Contact adding states
  const [isAddingContact, setIsAddingContact] = useState(false);
  const [addingContactData, setAddingContactData] = useState({
    fullName: "",
    email: "",
    phoneNumber: ""
  });

  // Dialer states
  const [showDialer, setShowDialer] = useState(false);
  const [dialerContact, setDialerContact] = useState<Contact | null>(null);
  const [agents, setAgents] = useState<any[]>([]);
  const [phoneNumbers, setPhoneNumbers] = useState<any[]>([]);
  const [selectedAgent, setSelectedAgent] = useState("");
  const [selectedFromNumber, setSelectedFromNumber] = useState("");
  const [isLoadingCall, setIsLoadingCall] = useState(false);
  const [contactToDelete, setContactToDelete] = useState<Contact | null>(null);

  // Call control states
  const [isCallActive, setIsCallActive] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [isOnHold, setIsOnHold] = useState(false);
  const [currentCallId, setCurrentCallId] = useState<string | null>(null);
  const [callDuration, setCallDuration] = useState(0);
  const [callTimer, setCallTimer] = useState<NodeJS.Timeout | null>(null);
  const [callControlUrl, setCallControlUrl] = useState<string | null>(null);
  const [callData, setCallData] = useState<any>(null);

  // Call history states
  const [callHistory, setCallHistory] = useState<CallHistoryItem[]>([]);
  const [callHistoryLoading, setCallHistoryLoading] = useState(false);
  const [callHistoryPage, setCallHistoryPage] = useState(1);
  const [callHistoryTotal, setCallHistoryTotal] = useState(0);
  const [selectedCallForRecording, setSelectedCallForRecording] = useState<CallHistoryItem | null>(null);
  const [showRecordingPlayer, setShowRecordingPlayer] = useState(false);

  // Chat sidebar states
  const [showChatSidebar, setShowChatSidebar] = useState(false);
  const [chatContact, setChatContact] = useState<Contact | null>(null);

  // Email modal states
  const [showEmailModal, setShowEmailModal] = useState(false);
  const [emailContact, setEmailContact] = useState<Contact | null>(null);
  const [emailData, setEmailData] = useState({
    to: "",
    cc: "",
    bcc: "",
    subject: "",
    body: "",
    attachments: [] as string[]
  });
  const [isSendingEmail, setIsSendingEmail] = useState(false);
  const [gmailConnected, setGmailConnected] = useState(false);
  const [checkingGmailStatus, setCheckingGmailStatus] = useState(false);
  const [smtpAvailable, setSmtpAvailable] = useState(false);
  const [checkingEmailServices, setCheckingEmailServices] = useState(false);
  const [connectedGmailEmail, setConnectedGmailEmail] = useState<string>("");
  const [isConnectingGmail, setIsConnectingGmail] = useState(false);
  const [isUnlinkingGmail, setIsUnlinkingGmail] = useState(false);

  // Check email services availability
  const checkEmailServices = async () => {
    setCheckingEmailServices(true);
    try {
      // Check Contact Gmail status (for contact email functionality)
      setCheckingGmailStatus(true);
      try {
        const contactGmailStatus = await getContactGmailStatus();
        setGmailConnected(contactGmailStatus.isConnected);

        // Set connected Gmail email if available
        if (contactGmailStatus.isConnected && contactGmailStatus.email) {
          setConnectedGmailEmail(contactGmailStatus.email);
        } else {
          setConnectedGmailEmail("");
        }
      } catch (error) {
        console.error('Error checking Contact Gmail status:', error);
        setGmailConnected(false);
        setConnectedGmailEmail("");
      } finally {
        setCheckingGmailStatus(false);
      }

      // Check SMTP service status
      try {
        const smtpStatus = await getEmailServiceStatus();
        setSmtpAvailable(smtpStatus.isConfigured);
      } catch (error) {
        console.error('Error checking SMTP status:', error);
        setSmtpAvailable(false);
      }

      return { gmailConnected: gmailConnected, smtpAvailable: smtpAvailable };
    } finally {
      setCheckingEmailServices(false);
    }
  };

  // Legacy function for backward compatibility
  const checkGmailStatus = async () => {
    const result = await checkEmailServices();
    return result.gmailConnected;
  };

  // Handle Contact Gmail connection
  const handleConnectGmailForContacts = async () => {
    setIsConnectingGmail(true);
    try {
      await connectContactGmail();
      // Refresh email services status after connection
      await checkEmailServices();
    } catch (error) {
      console.error("Error connecting Contact Gmail:", error);
    } finally {
      setIsConnectingGmail(false);
    }
  };

  // Handle Contact Gmail unlink
  const handleUnlinkGmailForContacts = async () => {
    setIsUnlinkingGmail(true);
    try {
      await disconnectContactGmail();
      // Refresh email services status after disconnection
      await checkEmailServices();
    } catch (error) {
      console.error("Error unlinking Contact Gmail:", error);
    } finally {
      setIsUnlinkingGmail(false);
    }
  };

  // Handle Add List button click with credit checking
  const handleAddListClick = async () => {
    if (isSuperAdmin) {
      setShowAddListSheet(true);
      return;
    }

    try {
      const response = await axiosInstance.get('/api/credits/balance');
      const balance = response.data.data;

      if (!balance || parseFloat(balance.available_credits) < 1) {
        setCurrentBalance(balance?.available_credits || 0);
        setShowInsufficientCreditsModal(true);
        return;
      }

      setShowAddListSheet(true);
    } catch (error) {
      console.error('Error checking credit balance:', error);
      toast.error('Unable to verify credit balance. Please try again.');
    }
  };

  // Fetch contact lists
  const fetchLists = async () => {
    try {
      setLoading(true);
      const response = await getAllContactLists(1, listSearch, isSuperAdmin);
      if (response.success) {
        // Ensure response.data is an array
        const listsData = Array.isArray(response.data) ? response.data : [];
        setLists(listsData);
      } else {
        setLists([]);
      }
    } catch (error) {
      console.error("Error fetching lists:", error);
      setLists([]);
    } finally {
      setLoading(false);
    }
  };

  // Check email services on component mount
  useEffect(() => {
    checkEmailServices();
  }, []);

  // Listen for Gmail connection success (when user returns from OAuth)
  useEffect(() => {
    const handleFocus = () => {
      // Re-check email services when window regains focus (user returns from OAuth)
      checkEmailServices();
    };

    window.addEventListener('focus', handleFocus);
    return () => window.removeEventListener('focus', handleFocus);
  }, []);

  // Fetch contacts for selected list
  const fetchContacts = async (listId: number) => {
    try {
      setContactsLoading(true);
      const response = await getContactsForList(listId.toString(), currentPage, contactSearch);
      console.log("Full API response:", response);

      if (response.success) {
        // The contacts are nested under response.data.contacts
        const contactsData = Array.isArray(response.data?.contacts) ? response.data.contacts : [];
        console.log("Extracted contacts:", contactsData);
        setContacts(contactsData);
        setTotalPages(response.data?.pagination?.totalPages || 1);
      } else {
        console.log("Response not successful:", response);
        setContacts([]);
      }
    } catch (error) {
      console.error("Error fetching contacts:", error);

      // Type guard for axios error
      const isAxiosError = (err: unknown): err is { response?: { status?: number; data?: any }; message?: string } => {
        return typeof err === 'object' && err !== null;
      };

      // Type guard for error with message
      const hasMessage = (err: unknown): err is { message: string } => {
        return typeof err === 'object' && err !== null && 'message' in err && typeof (err as any).message === 'string';
      };

      // Check if it's a "no contacts found" scenario
      const isNoContactsFound = isAxiosError(error) && (
        error.response?.status === 404 ||
        error.response?.data?.message?.toLowerCase().includes('not found') ||
        error.response?.data?.error?.toLowerCase().includes('not found')
      );

      // Only show toast for actual errors, not when no contacts are found
      if (!isNoContactsFound) {
        if (isAxiosError(error)) {
          console.error("Error details:", error.response?.data || error.message);
          const errorMessage = error.response?.data?.error || error.message || 'Unknown error occurred';
          toast.error(`Failed to fetch contacts: ${errorMessage}`);
        } else if (hasMessage(error)) {
          console.error("Error details:", error.message);
          toast.error(`Failed to fetch contacts: ${error.message}`);
        } else {
          console.error("Error details:", String(error));
          toast.error("Failed to fetch contacts: Unknown error occurred");
        }
      } else {
        console.error("Error details:", error.response?.data || error.message);
      }

      setContacts([]);
    } finally {
      setContactsLoading(false);
    }
  };

  // Effects
  useEffect(() => {
    fetchLists();
  }, [listSearch, isSuperAdmin]);

  useEffect(() => {
    if (selectedList) {
      fetchContacts(selectedList.id);
    }
  }, [selectedList, currentPage, contactSearch]);

  // Fetch call history when contact changes or call history page changes
  useEffect(() => {
    if (selectedContact && selectedContact.phoneNumber) {
      fetchCallHistory(selectedContact);
    }
  }, [selectedContact, callHistoryPage]);

  // Handlers
  const handleListSelect = (list: ContactList) => {
    if (list.is_deleted && !isSuperAdmin) {
      toast.error("This list has been disabled and is not accessible.");
      return;
    }
    console.log("Selected list:", list);
    setSelectedList(list);
    setSelectedContact(null);
    setIsEditingContact(false);
    setIsAddingContact(false); // Close add contact form when switching lists
    setCurrentPage(1);
  };

  const handleContactSelect = (contact: Contact) => {
    setSelectedContact(contact);
    // Reset call history when selecting a new contact
    setCallHistory([]);
    setCallHistoryPage(1);
    setCallHistoryTotal(0);
  };

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
  };

  // Fetch call history for selected contact
  const fetchCallHistory = async (contact: Contact) => {
    if (!contact.phoneNumber) return;

    try {
      setCallHistoryLoading(true);
      const response = await getContactCallHistory(contact.phoneNumber, callHistoryPage, 10);
      setCallHistory(response.calls);
      setCallHistoryTotal(response.totalCalls);
    } catch (error) {
      console.error("Error fetching call history:", error);
      setCallHistory([]);
      setCallHistoryTotal(0);
    } finally {
      setCallHistoryLoading(false);
    }
  };

  // Handle recording playback
  const handlePlayRecording = async (call: CallHistoryItem) => {
    if (!hasRecording(call)) return;

    try {
      setSelectedCallForRecording(call);
      setShowRecordingPlayer(true);
    } catch (error) {
      console.error("Error playing recording:", error);
      toast.error("Failed to play recording");
    }
  };



  // Handle edit contact
  const handleEditContact = (contact: Contact) => {
    setEditingContactData({
      fullName: contact.fullName || "",
      email: contact.email || "",
      phoneNumber: contact.phoneNumber || ""
    });
    setIsEditingContact(true);
  };

  // Handle save contact edit
  const handleSaveContactEdit = async () => {
    if (!selectedContact) return;

    // Validate phone number format (+1 followed by 10 digits)
    const phoneRegex = /^\+1[0-9]{10}$/;
    if (editingContactData.phoneNumber && !phoneRegex.test(editingContactData.phoneNumber)) {
      toast.error("Phone Number must start with +1 followed by 10 digits (e.g., +1234567890)");
      return;
    }

    try {
      await updateContact(selectedContact.id.toString(), editingContactData);
      toast.success("Contact updated successfully");
      setIsEditingContact(false);

      // Refresh contacts list
      if (selectedList) {
        fetchContacts(selectedList.id);
      }

      // Update selected contact
      setSelectedContact({
        ...selectedContact,
        ...editingContactData
      });
    } catch (error) {
      console.error("Error updating contact:", error);
      // Error toast is handled in the service
    }
  };

  // Handle cancel edit
  const handleCancelEdit = () => {
    setIsEditingContact(false);
    setEditingContactData({
      fullName: "",
      email: "",
      phoneNumber: ""
    });
  };

  // Handle save new contact
  const handleSaveNewContact = async () => {
    if (!selectedList || !addingContactData.fullName || !addingContactData.email || !addingContactData.phoneNumber) {
      toast.error("Please fill in all required fields");
      return;
    }

    // Validate phone number format (+1 followed by 10 digits)
    const phoneRegex = /^\+1[0-9]{10}$/;
    if (!phoneRegex.test(addingContactData.phoneNumber)) {
      toast.error("Phone Number must start with +1 followed by 10 digits (e.g., +1234567890)");
      return;
    }

    try {
      await addContactToList(selectedList.id.toString(), {
        fullName: addingContactData.fullName,
        email: addingContactData.email,
        contact_number: addingContactData.phoneNumber,
        phoneNumber: addingContactData.phoneNumber
      });

      toast.success("Contact added successfully");

      // Refresh contacts list
      fetchContacts(selectedList.id);

      // Update the contact count for the selected list
      setLists(prevLists =>
        prevLists.map(list =>
          list.id === selectedList.id
            ? { ...list, contactCount: list.contactCount + 1 }
            : list
        )
      );

      // Close add contact form
      setIsAddingContact(false);
      setAddingContactData({
        fullName: "",
        email: "",
        phoneNumber: ""
      });
    } catch (error) {
      console.error("Error adding contact:", error);
      // Error toast is handled in the service
    }
  };

  // Handle cancel add contact
  const handleCancelAddContact = () => {
    setIsAddingContact(false);
    setAddingContactData({
      fullName: "",
      email: "",
      phoneNumber: ""
    });
  };

  // Load agents and phone numbers for dialer
  const loadDialerData = async () => {
    try {
      const [agentsResponse, phoneNumbersResponse] = await Promise.all([
        fetchAllAgentsComplete(""),
        getAllPhoneNumbers()
      ]);

      console.log("ðŸ” Debug - Loaded dialer data:", {
        agentsCount: agentsResponse?.data?.length || 0,
        phoneNumbersCount: phoneNumbersResponse?.data?.length || 0,
        sampleAgent: agentsResponse?.data?.[0] ? {
          id: agentsResponse.data[0].id,
          assistant_id: agentsResponse.data[0].assistant_id,
          name: agentsResponse.data[0].name
        } : null
      });

      // Handle agents response - it returns { data: Assistant[], total: number }
      setAgents(agentsResponse?.data || []);
      setPhoneNumbers(phoneNumbersResponse?.data || []);
    } catch (error) {
      console.error("Error loading dialer data:", error);
    }
  };

  // Handle phone button click
  const handlePhoneClick = async (contact: Contact) => {
    if (!contact.phoneNumber) {
      toast.error("Contact has no phone number");
      return;
    }

    setDialerContact(contact);
    setShowDialer(true);

    // Load agents and phone numbers if not already loaded
    if (agents.length === 0 || phoneNumbers.length === 0) {
      await loadDialerData();
    }
  };

  // Handle chat button click
  const handleChatClick = (contact: Contact) => {
    if (!contact.phoneNumber) {
      toast.error("Contact has no phone number for messaging");
      return;
    }

    setChatContact(contact);
    setShowChatSidebar(true);
  };

  // Handle email button click
  const handleEmailClick = (contact: Contact) => {
    if (!contact.email) {
      toast.error("Contact has no email address");
      return;
    }

    setEmailContact(contact);
    setEmailData({
      to: contact.email,
      cc: "",
      bcc: "",
      subject: "",
      body: "",
      attachments: []
    });
    setShowEmailModal(true);
  };

  // Handle send email
  const handleSendEmail = async () => {
    if (!emailData.to || !emailData.subject || !emailData.body) {
      toast.error("Please fill in To, Subject, and Body fields");
      return;
    }

    // Check email services availability
    const services = await checkEmailServices();

    if (!services.gmailConnected && !services.smtpAvailable) {
      toast.error("No email service available. Please connect Gmail or configure SMTP.");
      return;
    }

    setIsSendingEmail(true);
    try {
      // Try Contact Gmail first if connected, otherwise use SMTP
      if (services.gmailConnected) {
        await sendContactGmailEmail({
          to: emailData.to,
          cc: emailData.cc || undefined,
          bcc: emailData.bcc || undefined,
          subject: emailData.subject,
          body: emailData.body,
          isHtml: false
        });
      } else if (services.smtpAvailable) {
        await sendSMTPEmail({
          to: emailData.to,
          cc: emailData.cc || undefined,
          bcc: emailData.bcc || undefined,
          subject: emailData.subject,
          body: emailData.body,
          isHtml: false
        });
      }

      setShowEmailModal(false);
      setEmailData({
        to: "",
        cc: "",
        bcc: "",
        subject: "",
        body: "",
        attachments: []
      });
    } catch (error) {
      console.error("Error sending email:", error);
      // Error toast is handled in the service
    } finally {
      setIsSendingEmail(false);
    }
  };

  // Format phone number to E.164 format
  const formatPhoneToE164 = (phoneNumber: string): string => {
    // Remove all non-digit characters
    let cleaned = phoneNumber.replace(/\D/g, '');

    // Handle different cases
    if (cleaned.length === 10) {
      // US number without country code (e.g., 5551234567)
      cleaned = '1' + cleaned;
    } else if (cleaned.length === 11 && cleaned.startsWith('1')) {
      // US number with country code (e.g., 15551234567)
      // Keep as is
    } else if (cleaned.length < 10) {
      // Invalid number - too short
      throw new Error('Phone number is too short');
    }
    // For other lengths, assume it already has the correct country code

    // Add the + prefix
    return '+' + cleaned;
  };

  // Handle make call
  const handleMakeCall = async () => {
    if (!dialerContact || !selectedAgent || !selectedFromNumber) {
      toast.error("Please select an agent and from number");
      return;
    }

    console.log("ðŸ” Debug - Making call with:", {
      selectedAgent,
      availableAgents: agents.map(a => ({ id: a.id, assistant_id: a.assistant_id, name: a.name })),
      dialerContact: dialerContact.fullName,
      selectedFromNumber
    });

    // Find the selected agent - try both id and assistant_id
    let selectedAgentData = agents.find(agent => agent.id === selectedAgent);
    if (!selectedAgentData) {
      selectedAgentData = agents.find(agent => agent.assistant_id === selectedAgent);
    }

    if (!selectedAgentData) {
      console.error("âŒ Selected agent not found in agents array:", {
        selectedAgent,
        availableAgentIds: agents.map(a => a.id),
        availableAssistantIds: agents.map(a => a.assistant_id)
      });
      toast.error("Selected agent not found");
      return;
    }

    // Use assistant_id (VAPI UUID) if available, otherwise use id
    const assistantUUID = selectedAgentData.assistant_id || selectedAgentData.id;

    console.log("Making call with:", {
      selectedAgentData,
      assistantUUID,
      originalPhoneNumber: dialerContact.phoneNumber,
      selectedFromNumber
    });

    setIsLoadingCall(true);
    try {
      // Format phone number to E.164
      const formattedPhoneNumber = formatPhoneToE164(dialerContact.phoneNumber);

      console.log("Formatted phone number:", formattedPhoneNumber);
      const callResponse = await createOutboundCall({
        assistantId: assistantUUID, // Use the VAPI UUID
        phoneNumber: formattedPhoneNumber, // Use E.164 formatted number
        phoneNumberId: selectedFromNumber, // Use the selected phone number ID
        metadata: {
          contactId: dialerContact.id,
          contactName: dialerContact.fullName,
          listId: selectedList?.id,
          listName: selectedList?.list_name
        }
      });

      console.log("ðŸ“ž Call response:", callResponse);
      console.log("ðŸ“ž Call response structure:", {
        id: callResponse.id,
        callId: callResponse.callId,
        data: callResponse.data,
        monitor: callResponse.monitor,
        dataMonitor: callResponse.data?.monitor,
        controlUrl: callResponse.monitor?.controlUrl || callResponse.data?.monitor?.controlUrl
      });

      toast.success(`Call initiated to ${dialerContact.fullName}`);

      // Store call data for control operations
      setCallData(callResponse);
      const callId = callResponse.id || callResponse.callId || callResponse.data?.id;
      const controlUrl = callResponse.monitor?.controlUrl || callResponse.data?.monitor?.controlUrl;

      console.log("ðŸ“ž Extracted call control data:", { callId, controlUrl });

      setCurrentCallId(callId);
      setCallControlUrl(controlUrl);

      // Set call as active and start timer
      setIsCallActive(true);
      setCallDuration(0);

      // Start call timer
      const timer = setInterval(() => {
        setCallDuration(prev => prev + 1);
      }, 1000);
      setCallTimer(timer);

      // Don't close the modal - keep it open for call controls
      // setShowDialer(false);
      // setSelectedAgent("");
      // setSelectedFromNumber("");
    } catch (error) {
      console.error("Error making call:", error);
      // Error toast is handled in the service
    } finally {
      setIsLoadingCall(false);
    }
  };

  // Call control functions
  const handleMuteToggle = async () => {
    console.log("ðŸ”‡ Mute toggle - Call control data:", {
      currentCallId,
      callControlUrl,
      isMuted,
      callData
    });

    if (!currentCallId || !callControlUrl) {
      console.error("âŒ Call control not available:", { currentCallId, callControlUrl });
      toast.error("Call control not available - missing call ID or control URL");
      return;
    }

    try {
      if (isMuted) {
        await unmuteCall(currentCallId, callControlUrl);
        setIsMuted(false);
      } else {
        await muteCall(currentCallId, callControlUrl);
        setIsMuted(true);
      }
    } catch (error) {
      console.error("Error toggling mute:", error);
      // Toast error is handled in the service functions
    }
  };

  const handleHoldToggle = () => {
    // Note: VAPI doesn't have a direct hold/resume API
    // This is a UI-only feature for now
    setIsOnHold(!isOnHold);
    toast.success(isOnHold ? "Call resumed" : "Call on hold");
    // TODO: Implement actual hold/resume when VAPI supports it
  };

  const handleEndCall = async () => {
    console.log("ðŸ“ž End call - Call control data:", {
      currentCallId,
      callControlUrl,
      callData
    });

    if (!currentCallId || !callControlUrl) {
      console.error("âŒ Call control not available:", { currentCallId, callControlUrl });

      // Clear call timer and close modal even if call control is not available
      if (callTimer) {
        clearInterval(callTimer);
        setCallTimer(null);
      }

      setIsCallActive(false);
      setIsMuted(false);
      setIsOnHold(false);
      setCurrentCallId(null);
      setCallControlUrl(null);
      setCallData(null);
      setCallDuration(0);

      setShowDialer(false);
      setSelectedAgent("");
      setSelectedFromNumber("");
      return;
    }

    // Close modal and clean up state immediately for better UX
    if (callTimer) {
      clearInterval(callTimer);
      setCallTimer(null);
    }

    setIsCallActive(false);
    setIsMuted(false);
    setIsOnHold(false);
    setCurrentCallId(null);
    setCallControlUrl(null);
    setCallData(null);
    setCallDuration(0);

    setShowDialer(false);
    setSelectedAgent("");
    setSelectedFromNumber("");

    // Attempt to end call in background without blocking UI
    try {
      endCallViaControl(currentCallId, callControlUrl).catch(error => {
        console.error("Background call ending failed:", error);
        // Silently handle the error - UI is already updated
      });
    } catch (error) {
      console.error("Error initiating call end:", error);
      // Silently handle the error - UI is already updated
    }
  };

  // Format call duration
  const formatCallDuration = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  // Handle add contact to list
  const handleAddContactToList = () => {
    if (selectedList) {
      // Clear any selected contact and open add contact form in sidebar
      setSelectedContact(null);
      setIsEditingContact(false);
      setIsAddingContact(true);
      setAddingContactData({
        fullName: "",
        email: "",
        phoneNumber: ""
      });
    }
  };

  // Handle delete contact confirmation
  const handleDeleteContactConfirm = (contact: Contact) => {
    setContactToDelete(contact);
  };

  // Handle delete contact
  const handleDeleteContact = async () => {
    if (!selectedList || !contactToDelete) return;

    try {
      await removeContactFromList(selectedList.id.toString(), contactToDelete.id.toString());
      toast.success("Contact removed from list successfully");

      // Refresh contacts list
      fetchContacts(selectedList.id);

      // Update the contact count for the selected list
      setLists(prevLists =>
        prevLists.map(list =>
          list.id === selectedList.id
            ? { ...list, contactCount: Math.max(0, list.contactCount - 1) }
            : list
        )
      );

      // Close contact panel if deleted contact was selected
      if (selectedContact?.id === contactToDelete.id) {
        setSelectedContact(null);
      }

      // Close confirmation dialog
      setContactToDelete(null);
    } catch (error) {
      console.error("Error removing contact:", error);
      // Error toast is handled in the service
    }
  };

  // Utility functions

  const getInitials = (email: string) => {
    return email.substring(0, 2).toUpperCase();
  };

  const formatPhoneNumber = (phone: string) => {
    if (!phone) return "N/A";
    return phone.replace(/(\d{3})(\d{3})(\d{4})/, "($1) $2-$3");
  };

  // Filter lists based on search
  const filteredLists = Array.isArray(lists) ? lists.filter(list =>
    list.list_name.toLowerCase().includes(listSearch.toLowerCase())
  ) : [];

  // Filter contacts based on search
  const filteredContacts = Array.isArray(contacts) ? contacts.filter(contact =>
    contact.email.toLowerCase().includes(contactSearch.toLowerCase()) ||
    contact.fullName?.toLowerCase().includes(contactSearch.toLowerCase())
  ) : [];

  return (
    <Page>
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-3">
          <h1 className="text-2xl font-bold text-white">Contacts</h1>
          <Badge variant="secondary" className="px-3 py-1 bg-indigo-500/20 text-indigo-300 border border-indigo-500/30">
            <span>{filteredLists.length}</span>
          </Badge>
          <span className="text-sm text-white/65">Total Contact groups: {filteredLists.length}</span>
        </div>
        <div className="flex items-center space-x-2">

          <button onClick={handleAddListClick} className=" px-5 py-2
    rounded-lg
    text-sm font-semibold
    text-white
    bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
    border border-white/15
    shadow-[0_2px_5px_rgba(90,110,255,0.45)]
    hover:shadow-[0_5px_10px_rgba(90,110,255,0.65)]
    hover:bg-[linear-gradient(180deg,rgba(90,110,255,0.6),rgba(36,46,151,0.45))]
    disabled:opacity-40
    disabled:cursor-not-allowed
    transition-all
    flex items-center gap-2">
            <Plus className="h-4 w-8 mr-2" />
            Add Contacts in List
          </button>
          {/* Debug button - remove after testing */}



        </div>
      </div>

      {/* Three Panel Layout */}
      <div className="flex h-[calc(100vh-200px)]  gap-4">
        {/* Left Panel - Contact Groups */}
        <div className="w-80  bg-[#0b0f2e]/90 backdrop-blur-sm border border-white/10 rounded-lg flex flex-col">
          <div className="p-4 border-b border-white/10 ">
            <h2 className="text-lg font-semibold mb-3 text-white">Contact Groups</h2>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-white/50" />
              <Input
                placeholder="Search"
                value={listSearch}
                onChange={(e) => setListSearch(e.target.value)}
                className="pl-10 bg-white/5 border-white/20 text-white placeholder:text-white/40"
              />
            </div>
          </div>
          <div className="flex-1 neon-fire-scroll  overflow-y-auto">
            {loading ? (
              <div className="p-4 text-center text-white/50">Loading...</div>
            ) : filteredLists.length === 0 ? (
              <div className="p-4 text-center text-white/50">No groups found</div>
            ) : (
              <div className="p-2">
                {filteredLists.map((list) => (
                  <div
                    key={list.id}
                    onClick={() => handleListSelect(list)}
                    className={`flex items-center p-3 rounded-lg cursor-pointer mb-1 transition-colors ${
                      selectedList?.id === list.id
                        ? 'bg-indigo-600/30 border border-indigo-500/50 text-white'
                        : 'hover:bg-white/5 text-white/80'
                    } ${list.is_deleted ? 'opacity-50' : ''}`}
                  >
                    <div className="flex items-center space-x-3 flex-1">
                      <Avatar className="h-8 w-8">
                        <AvatarFallback className={selectedList?.id === list.id ? 'bg-indigo-600 text-white' : 'bg-white/10 text-white'}>
                          {list.list_name.substring(0, 2).toUpperCase()}
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <div className="font-medium truncate text-white">
                          {list.list_name}
                          {/* {list.is_deleted && (
                            <span className="ml-2 text-xs bg-red-100 text-red-600 px-2 py-1 rounded-full">
                              Disabled
                            </span>
                          )} */}
                        </div>
                        <div className={`text-xs ${selectedList?.id === list.id ? 'text-white/70' : 'text-white/50'}`}>
                          {list.contactCount} contacts
                        </div>
                      </div>
                      <Badge variant="secondary" className="text-xs bg-white/10 text-white/70 border-white/20">
                        {list.contactCount}
                      </Badge>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Middle Panel - Contacts List */}
        <div className="flex-1 bg-[#0b0f2e]/90 backdrop-blur-sm border border-white/10 rounded-lg flex flex-col">
          <div className="p-4 border-b border-white/10">
            <div className="flex items-center justify-between mb-3">
              <div>
                <h2 className="text-lg font-semibold text-white">
                  {selectedList ? selectedList.list_name : "Default"}
                </h2>
                {selectedList && (
                  <p className="text-sm text-white/50">
                    {filteredContacts.length} contacts
                  </p>
                )}
              </div>
              <DropdownMenu modal={false}>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="sm" aria-label="Contact options" className="text-white/70 hover:text-white hover:bg-white/10">
                          <MoreHorizontal className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end" side="bottom" sideOffset={5} className="bg-[#0e1340] border-white/10 text-white">
                        <DropdownMenuItem onClick={() => handleAddContactToList()} className="text-white hover:bg-white/10 focus:bg-white/10">
                          <UserPlus className="h-4 w-4 mr-2" />
                          Add Contacts
                        </DropdownMenuItem>
                        <DropdownMenuSeparator className="bg-white/10" />
                        <DropdownMenuItem
                          onClick={() => selectedContact && handleEditContact(selectedContact)}
                          disabled={!selectedContact}
                          className="text-white hover:bg-white/10 focus:bg-white/10"
                        >
                          <Edit className="h-4 w-4 mr-2" />
                          Edit Contact
                        </DropdownMenuItem>
                        <DropdownMenuSeparator className="bg-white/10" />
                        <DropdownMenuItem
                          onClick={() => selectedContact && handleDeleteContactConfirm(selectedContact)}
                          className="text-red-400 focus:text-red-400 hover:bg-white/10 focus:bg-white/10"
                          disabled={!selectedContact}
                        >
                          <Trash className="h-4 w-4 mr-2" />
                          Remove from List
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>

            </div>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-white/50" />
              <Input
                placeholder="Search"
                value={contactSearch}
                onChange={(e) => setContactSearch(e.target.value)}
                className="pl-10 bg-white/5 border-white/20 text-white placeholder:text-white/40"
              />
            </div>
          </div>
          <div className="flex-1 overflow-y-auto">
            {!selectedList ? (
              <div className="flex items-center justify-center h-full text-white/50">
                <div className="text-center">
                  <Users className="h-12 w-12 mx-auto mb-4 text-white/30" />
                  <p>Select a contact group to view contacts</p>
                </div>
              </div>
            ) : contactsLoading ? (
              <div className="p-4 text-center text-white/50">Loading contacts...</div>
            ) : filteredContacts.length === 0 ? (
              <div className="flex items-center justify-center h-full text-white/50">
                <div className="text-center">
                  <Mail className="h-12 w-12 mx-auto mb-4 text-white/30" />
                  <p>No contacts found in this group</p>
                </div>
              </div>
            ) : (
              <div className="p-2">
                {filteredContacts.map((contact) => (
                  <div
                    key={contact.id}
                    onClick={() => handleContactSelect(contact)}
                    className={`flex items-center p-3 rounded-lg cursor-pointer mb-1 transition-colors ${
                      selectedContact?.id === contact.id
                        ? 'bg-indigo-600/30 border border-indigo-500/50'
                        : 'hover:bg-white/5'
                    }`}
                  >
                    <div className="flex items-center space-x-3 flex-1">
                      <Avatar className="h-10 w-10">
                        <AvatarFallback className="bg-green-600/20 text-green-400">
                          {getInitials(contact.email)}
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 min-w-0">
                        <div className="font-medium text-white">
                          {contact.fullName || "No Name"}
                        </div>
                        <div className="text-sm text-white/50 truncate">
                          {contact.email}
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                      <Button
                        size="sm"
                        className="rounded-full bg-green-600 hover:bg-green-700 text-white shadow-md p-2"
                        onClick={(e) => {
                          e.stopPropagation();
                          handlePhoneClick(contact);
                        }}
                      >
                        <Phone className="h-4 w-4" />
                      </Button>

                      <Button
                        size="sm"
                        className="rounded-full bg-orange-600 hover:bg-orange-700 text-white shadow-md p-2"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleEmailClick(contact);
                        }}
                      >
                        <Mail className="h-4 w-4" />
                      </Button>

                      <Button
                        size="sm"
                        className="rounded-full bg-blue-600 hover:bg-blue-700 text-white shadow-md p-2"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleChatClick(contact);
                        }}
                      >
                        <MessageSquare className="h-4 w-4" />
                      </Button>

                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
          
        </div>

        {/* Right Panel - Contact Details */}
        <div className="w-96 bg-[#0b0f2e]/90 backdrop-blur-sm border border-white/10 rounded-lg flex flex-col">
          {isAddingContact ? (
            <>
              {/* Add Contact Header */}
              <div className="p-6 border-b border-white/10">
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center space-x-3">
                    <div className="h-16 w-16 bg-indigo-600/20 rounded-full flex items-center justify-center">
                      <UserPlus className="h-8 w-8 text-indigo-400" />
                    </div>
                    <div>
                      <h3 className="text-xl font-semibold text-white">Add New Contact</h3>
                      <p className="text-sm text-white/50">Add contact to {selectedList?.list_name}</p>
                    </div>
                  </div>
                  <div className="flex space-x-2">
                    <Button variant="outline" size="sm" onClick={handleCancelAddContact} className="border-white/20 text-white hover:bg-white/10">
                      Cancel
                    </Button>
                    <Button size="sm" onClick={handleSaveNewContact} className="bg-gradient-to-r from-indigo-600 to-purple-600">
                      Save
                    </Button>
                  </div>
                </div>
              </div>

              {/* Add Contact Form */}
              <div className="flex-1 overflow-y-auto p-6">
                <div className="space-y-6">
                  <div>
                    <h4 className="text-sm font-medium text-white mb-3">Contact Information</h4>
                    <div className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium text-white/70 mb-1">
                          Full Name *
                        </label>
                        <Input
                          value={addingContactData.fullName}
                          onChange={(e) => setAddingContactData(prev => ({ ...prev, fullName: e.target.value }))}
                          placeholder="Enter full name"
                          className="w-full bg-white/5 border-white/20 text-white placeholder:text-white/40"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-white/70 mb-1">
                          Email Address *
                        </label>
                        <Input
                          type="email"
                          value={addingContactData.email}
                          onChange={(e) => setAddingContactData(prev => ({ ...prev, email: e.target.value }))}
                          placeholder="Enter email address"
                          className="w-full bg-white/5 border-white/20 text-white placeholder:text-white/40"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-white/70 mb-1">
                          Phone Number *
                        </label>
                        <Input
                          type="tel"
                          value={addingContactData.phoneNumber}
                          onChange={(e) => setAddingContactData(prev => ({ ...prev, phoneNumber: e.target.value }))}
                          placeholder="Enter phone number with +1 (e.g., +1234567890)"
                          className="w-full bg-white/5 border-white/20 text-white placeholder:text-white/40"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          ) : !selectedContact ? (
            <div className="flex items-center justify-center h-full text-white/50">
              <div className="text-center">
                <Users className="h-12 w-12 mx-auto mb-4 text-white/30" />
                <p>Select a contact to view details</p>
              </div>
            </div>
          ) : (
            <>
              {/* Contact Header */}
              <div className="p-6 border-b border-white/10">
                <div className="flex items-center justify-between mb-4">
                  <Avatar className="h-16 w-16">
                    <AvatarFallback className="bg-green-600/20 text-green-400 text-xl">
                      {getInitials(selectedContact.email)}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex items-center space-x-2">
                  <Button
                    size="sm"
                    className="rounded-full bg-green-600 hover:bg-green-700 text-white shadow-md p-2"
                    onClick={() => selectedContact && handlePhoneClick(selectedContact)}
                  >
                    <Phone className="h-4 w-4" />
                  </Button>

                    
                    
                  </div>
                </div>
                <div>
                  <h3 className="text-xl font-semibold text-white mb-1">
                    {isEditingContact ? editingContactData.fullName || "No Name" : selectedContact.fullName || "No Name"}
                  </h3>
                  <p className="text-sm text-white/50">
                    {isEditingContact ? formatPhoneNumber(editingContactData.phoneNumber) : formatPhoneNumber(selectedContact.phoneNumber)}
                  </p>
                </div>
              </div>

              {/* Contact Details */}
              <div className="flex-1 overflow-y-auto p-6">
                <div className="space-y-6">
                  {/* General Info */}
                  <div>
                    <div className="flex items-center justify-between mb-3">
                      <h4 className="text-sm font-medium text-white">General Info</h4>
                      {isEditingContact && (
                        <div className="flex space-x-2">
                          <Button variant="outline" size="sm" onClick={handleCancelEdit} className="border-white/20 text-white hover:bg-white/10">
                            Cancel
                          </Button>
                          <Button size="sm" onClick={handleSaveContactEdit} className="bg-gradient-to-r from-indigo-600 to-purple-600">
                            Save
                          </Button>
                        </div>
                      )}
                    </div>

                    {isEditingContact ? (
                      <div className="space-y-4">
                        <div>
                          <label className="text-sm font-medium text-white/70 block mb-1">Full Name</label>
                          <Input
                            value={editingContactData.fullName}
                            onChange={(e) => setEditingContactData({
                              ...editingContactData,
                              fullName: e.target.value
                            })}
                            placeholder="Enter full name"
                            className="bg-white/5 border-white/20 text-white placeholder:text-white/40"
                          />
                        </div>
                        <div>
                          <label className="text-sm font-medium text-white/70 block mb-1">Phone Number</label>
                          <Input
                            value={editingContactData.phoneNumber}
                            onChange={(e) => setEditingContactData({
                              ...editingContactData,
                              phoneNumber: e.target.value
                            })}
                            placeholder="Enter phone number with +1 (e.g., +1234567890)"
                            className="bg-white/5 border-white/20 text-white placeholder:text-white/40"
                          />
                        </div>
                        <div>
                          <label className="text-sm font-medium text-white/70 block mb-1">Email Address</label>
                          <Input
                            value={editingContactData.email}
                            onChange={(e) => setEditingContactData({
                              ...editingContactData,
                              email: e.target.value
                            })}
                            placeholder="Enter email address"
                            type="email"
                            className="bg-white/5 border-white/20 text-white placeholder:text-white/40"
                          />
                        </div>
                      </div>
                    ) : (
                      <div className="space-y-3">
                        <div className="flex items-center space-x-3">
                          <Phone className="h-4 w-4 text-white/50" />
                          <div className="flex-1">
                            <p className="text-sm font-medium text-white">Phone Number</p>
                            <p className="text-sm text-white/50">
                              {formatPhoneNumber(selectedContact.phoneNumber)}
                            </p>
                          </div>

                        </div>
                        <div className="flex items-center space-x-3">
                          <Mail className="h-4 w-4 text-white/50" />
                          <div>
                            <p className="text-sm font-medium text-white">Email Address</p>
                            <p className="text-sm text-white/50">{selectedContact.email}</p>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>

                  {/* Labels */}
                  <div>
                    <div className="flex items-center justify-between mb-3">
                      <h4 className="text-sm font-medium text-white">Labels</h4>
                      <Button variant="ghost" size="sm" className="text-white/70 hover:text-white hover:bg-white/10">
                        <Plus className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>

                  {/* Tabs for Notes, Call History, Chat History */}
                  <div>
                    <Tabs defaultValue="notes" className="w-full">
                      <TabsList className="grid w-full grid-cols-3 bg-white/5">
                        <TabsTrigger value="notes" className="text-xs text-white/70 data-[state=active]:bg-white/10 data-[state=active]:text-white">Notes</TabsTrigger>
                        <TabsTrigger value="calls" className="text-xs text-white/70 data-[state=active]:bg-white/10 data-[state=active]:text-white">Call History</TabsTrigger>
                        <TabsTrigger value="chats" className="text-xs text-white/70 data-[state=active]:bg-white/10 data-[state=active]:text-white">Chat History</TabsTrigger>
                      </TabsList>
                      <TabsContent value="notes" className="mt-4">
                        <div className="text-center py-8">
                          <FileText className="h-12 w-12 mx-auto mb-4 text-white/30" />
                          <p className="text-sm text-white/50 mb-4">No notes yet</p>
                          <p className="text-xs text-white/40">Add your first note using the text area above</p>
                        </div>
                      </TabsContent>
                      <TabsContent value="calls" className="mt-4">
                        {callHistoryLoading ? (
                          <div className="text-center py-8">
                            <Loader2 className="h-8 w-8 mx-auto mb-4 text-white/50 animate-spin" />
                            <p className="text-sm text-white/50">Loading call history...</p>
                          </div>
                        ) : callHistory.length === 0 ? (
                          <div className="text-center py-8">
                            <Phone className="h-12 w-12 mx-auto mb-4 text-white/30" />
                            <p className="text-sm text-white/50">No call history</p>
                          </div>
                        ) : (
                          <div className="space-y-3">
                            {callHistory.map((call) => {
                              const status = formatCallStatus(call.status);
                              const hasRec = hasRecording(call);

                              return (
                                <div key={call.call_id} className="border border-white/10 rounded-lg p-4 hover:bg-white/5 bg-white/5">
                                  <div className="flex items-center justify-between mb-2">
                                    <div className="flex items-center space-x-2">
                                      <Phone className="h-4 w-4 text-white/50" />
                                      <span className="text-sm font-medium text-white">
                                        {formatCallDate(call.created_at)}
                                      </span>
                                      <Badge variant="secondary" className={`text-xs ${status.color}`}>
                                        {status.text}
                                      </Badge>
                                    </div>
                                    <div className="flex items-center space-x-2">
                                      {hasRec && (
                                        <Button
                                          size="sm"
                                          variant="ghost"
                                          onClick={() => handlePlayRecording(call)}
                                          className="h-6 w-6 p-0 text-white/70 hover:text-white hover:bg-white/10"
                                        >
                                          <History className="h-3 w-3" />
                                        </Button>
                                      )}
                                      <span className="text-xs text-white/50">
                                        {formatDuration(call.duration)}
                                      </span>
                                    </div>
                                  </div>

                                  <div className="flex items-center justify-between text-xs text-white/50">
                                    <span>
                                      {call.assistant_name || 'Unknown Assistant'}
                                    </span>
                                    <span>
                                      {formatCallCost(call.cost)}
                                    </span>
                                  </div>
                                </div>
                              );
                            })}

                            {callHistoryTotal > 10 && (
                              <div className="text-center pt-4">
                                <Button
                                  variant="outline"
                                  size="sm"
                                  onClick={() => setCallHistoryPage(prev => prev + 1)}
                                  disabled={callHistory.length >= callHistoryTotal}
                                  className="border-white/20 text-white hover:bg-white/10"
                                >
                                  Load More
                                </Button>
                              </div>
                            )}
                          </div>
                        )}
                      </TabsContent>
                      <TabsContent value="chats" className="mt-4">
                        <div className="text-center py-8">
                          <MessageSquare className="h-12 w-12 mx-auto mb-4 text-white/30" />
                          <p className="text-sm text-white/50">No chat history</p>
                        </div>
                      </TabsContent>
                    </Tabs>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      </div>

      {/* Insufficient Credits Modal */}
      <Dialog open={showInsufficientCreditsModal} onOpenChange={setShowInsufficientCreditsModal}>
        <DialogContent className="sm:max-w-md bg-[#0e1340] border-white/10 text-white">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-full bg-red-500/20 flex items-center justify-center">
                <AlertCircle className="w-4 h-4 text-red-400" />
              </div>
              <div>
                <h3 className="text-lg font-semibold text-white">Insufficient Credits</h3>
                <p className="text-sm text-white/50 font-normal">You need credits to create contact lists</p>
              </div>
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4">
            <div className="bg-white/5 p-4 rounded-lg border border-white/10">
              <div className="flex justify-between items-center">
                <span className="text-sm text-white/70">Current Balance:</span>
                <span className="font-semibold text-white">{currentBalance} credits</span>
              </div>
              <div className="flex justify-between items-center mt-2">
                <span className="text-sm text-white/70">Required:</span>
                <span className="font-semibold text-red-400">1 credit minimum</span>
              </div>
            </div>
            <div className="flex gap-3">
              <Button
                variant="outline"
                onClick={() => setShowInsufficientCreditsModal(false)}
                className="flex-1 border-white/20 text-white hover:bg-white/10"
              >
                Cancel
              </Button>
              <Button
                onClick={() => {
                  setShowInsufficientCreditsModal(false);
                  window.location.href = '/credits';
                }}
                className="flex-1 bg-gradient-to-r from-indigo-600 to-purple-600"
              >
                Buy Credits
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

      {/* Add Contact List Sheet */}
      <Sheet open={showAddListSheet} onOpenChange={(open) => {
        setShowAddListSheet(open);
        // Refresh lists when sheet is closed (in case a new list was created)
        if (!open) {
          fetchLists();
        }
      }}>
        <SheetContent size="half" className="bg-[#0e1340] border-white/10">
          <AddContactList />
        </SheetContent>
      </Sheet>

      {/* Delete Contact Confirmation Dialog */}
      <AlertDialog open={!!contactToDelete} onOpenChange={() => setContactToDelete(null)}>
        <AlertDialogContent className="bg-[#0e1340] border-white/10 text-white">
          <AlertDialogHeader>
            <AlertDialogTitle className="text-white">Remove Contact from List</AlertDialogTitle>
            <AlertDialogDescription className="text-white/70">
              Are you sure you want to remove "{contactToDelete?.fullName || contactToDelete?.email}" from this list?
              This action cannot be undone.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel className="border-white/20 text-white hover:bg-white/10 bg-transparent">Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteContact}
              className="bg-red-600 hover:bg-red-700"
            >
              Remove Contact
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* Mobile-Style Dialer Modal */}
      {showDialer && (
        <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50 p-4">
          <div className="bg-[#0e1340] border border-white/10 rounded-3xl shadow-2xl w-full max-w-sm mx-auto overflow-hidden">
            {/* Header */}
            <div className={`px-6 py-4 text-white ${isCallActive ? 'bg-green-600' : 'bg-gradient-to-r from-indigo-600 to-purple-600'}`}>
              <div className="flex items-center justify-between">
                <h2 className="text-lg font-semibold flex items-center gap-2">
                  <Phone className="h-5 w-5" />
                  {isCallActive ? 'Active Call' : 'Make Call'}
                </h2>
                {!isCallActive && (
                  <button
                    onClick={() => setShowDialer(false)}
                    className="text-white hover:text-gray-200 transition-colors text-2xl leading-none"
                  >
                    Ã—
                  </button>
                )}
                {isCallActive && (
                  <div className="flex items-center gap-2 text-sm">
                    <Clock className="h-4 w-4" />
                    {formatCallDuration(callDuration)}
                  </div>
                )}
              </div>
            </div>

            {/* Contact Display */}
            <div className="px-6 py-6 bg-[#0b0f2e]">
              <div className="flex items-center space-x-4">
                <div className={`w-16 h-16 border-2 border-white/20 rounded-full flex items-center justify-center shadow-sm ${
                  isCallActive ? 'bg-green-500 animate-pulse' : 'bg-green-500'
                }`}>
                  <User className="h-8 w-8 text-white" />
                </div>
                <div className="flex-1">
                  <div className="rounded px-3 mb-2 bg-white/5 flex items-center justify-between">
                    <p className="text-sm text-white/50 mb-1">Name</p>
                    <p className="font-medium text-white">{dialerContact?.fullName}</p>
                  </div>
                  <div className="rounded px-3 bg-white/5 flex items-center justify-between">
                    <p className="text-sm text-white/50 mb-1">Number</p>
                    <p className="font-medium text-white">{dialerContact?.phoneNumber}</p>
                  </div>
                  {isCallActive && (
                    <div className="mt-2 text-center">
                      <div className={`inline-flex items-center gap-2 px-3 py-1 rounded-full text-sm font-medium ${
                        isOnHold ? 'bg-yellow-500/20 text-yellow-400' : 'bg-green-500/20 text-green-400'
                      }`}>
                        {isOnHold ? 'On Hold' : 'Connected'}
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Agent & From Number Selection - Only show when call is not active */}
            {!isCallActive && (
              <div className="px-6 py-4 space-y-4 bg-[#0b0f2e] border-t border-white/10">
                <div className="border border-white/20 rounded-lg bg-white/5">
                  <select
                    value={selectedAgent}
                    onChange={(e) => setSelectedAgent(e.target.value)}
                    className="w-full bg-transparent text-white px-4 py-4 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent appearance-none"
                  >
                    <option value="" className="bg-[#0e1340] text-white">Agent</option>
                    {agents.map((agent) => (
                      <option key={agent.id} value={agent.assistant_id || agent.id} className="bg-[#0e1340] text-white">
                        {agent.name || agent.id}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="border border-white/20 rounded-lg bg-white/5">
                  <select
                    value={selectedFromNumber}
                    onChange={(e) => setSelectedFromNumber(e.target.value)}
                    className="w-full bg-transparent text-white px-4 py-4 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent appearance-none"
                  >
                    <option value="" className="bg-[#0e1340] text-white">From Number</option>
                    {phoneNumbers.map((phoneNumber) => (
                      <option key={phoneNumber.id} value={phoneNumber.id} className="bg-[#0e1340] text-white">
                        {phoneNumber.number || phoneNumber.friendlyName}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
            )}

            {/* Action Buttons */}
            <div className="px-6 py-6 bg-[#0b0f2e] border-t border-white/10">
              {!isCallActive ? (
                /* Call Setup Buttons */
                <div className="flex items-center justify-center space-x-4">
                  {/* Cancel Button */}
                  <button
                    onClick={() => setShowDialer(false)}
                    className="flex-1 py-3 px-6 bg-red-600 text-white hover:bg-red-700 rounded-full font-medium transition-colors"
                  >
                    Cancel
                  </button>

                  {/* Call Button */}
                  <button
                    onClick={handleMakeCall}
                    disabled={!selectedAgent || !selectedFromNumber || isLoadingCall}
                    className="flex-1 py-3 bg-green-600 hover:bg-green-700 text-white px-6 rounded-full font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {isLoadingCall ? (
                      <div className="flex items-center justify-center">
                        <div className="w-4 h-4 border-2 border-white/40 border-t-transparent rounded-full animate-spin mr-2" />
                        Calling...
                      </div>
                    ) : (
                      "Call"
                    )}
                  </button>
                </div>
              ) : (
                /* Call Control Buttons */
                <div className="space-y-4">
                  {/* Primary Controls Row */}
                  <div className="flex items-center justify-center space-x-6">
                    {/* Mute Button */}
                    <button
                      onClick={handleMuteToggle}
                      className={`w-16 h-16 rounded-full flex items-center justify-center transition-all ${
                        isMuted
                          ? 'bg-red-500 hover:bg-red-600 text-white'
                          : 'bg-white/10 hover:bg-white/20 text-white'
                      }`}
                    >
                      {isMuted ? <MicOff className="h-6 w-6" /> : <Mic className="h-6 w-6" />}
                    </button>

                    {/* End Call Button */}
                    <button
                      onClick={handleEndCall}
                      className="w-20 h-20 bg-red-500 hover:bg-red-600 text-white rounded-full flex items-center justify-center transition-colors shadow-lg"
                    >
                      <PhoneOff className="h-8 w-8" />
                    </button>

                    {/* Hold Button */}
                    <button
                      onClick={handleHoldToggle}
                      className={`w-16 h-16 rounded-full flex items-center justify-center transition-all ${
                        isOnHold
                          ? 'bg-yellow-500 hover:bg-yellow-600 text-white'
                          : 'bg-white/10 hover:bg-white/20 text-white'
                      }`}
                    >
                      {isOnHold ? <Play className="h-6 w-6" /> : <Pause className="h-6 w-6" />}
                    </button>
                  </div>

                  {/* Control Labels */}
                  <div className="flex items-center justify-center space-x-6 text-xs text-white/50">
                    <span className="w-16 text-center">{isMuted ? 'Unmute' : 'Mute'}</span>
                    <span className="w-20 text-center">End Call</span>
                    <span className="w-16 text-center">{isOnHold ? 'Resume' : 'Hold'}</span>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Call Recording Player */}
      <CallRecordingPlayer
        call={selectedCallForRecording}
        isOpen={showRecordingPlayer}
        onClose={() => {
          setShowRecordingPlayer(false);
          setSelectedCallForRecording(null);
        }}
      />

      {/* Chat Sidebar */}
      {chatContact && (
        <ChatSidebar
          isOpen={showChatSidebar}
          contact={{
            id: chatContact.id.toString(),
            email: chatContact.email,
            contact_number: chatContact.phoneNumber,
            list_name: selectedList?.list_name
          }}
          onClose={() => {
            setShowChatSidebar(false);
            setChatContact(null);
          }}
        />
      )}

      {/* Email Modal */}
      <Dialog open={showEmailModal} onOpenChange={setShowEmailModal}>
        <DialogContent className="max-w-7xl max-h-[80vh] flex flex-col">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Mail className="h-5 w-5 text-orange-600" />
              Compose Email
            </DialogTitle>
            <DialogDescription>
              Send an email to {emailContact?.fullName || emailContact?.email}
            </DialogDescription>
          </DialogHeader>

          <div className="flex-1 overflow-y-auto space-y-4">
            {/* From Field with Gmail Connection */}
            <div>
              <Label htmlFor="from" className="text-sm font-medium text-gray-700 mb-1 block">
                From
              </Label>
              <div className="space-y-2">
                <Input
                  id="from"
                  value={connectedGmailEmail || ""}
                  disabled
                  className="bg-gray-50"
                  placeholder="Connect Gmail to send emails"
                />

                {/* Gmail Connection Links */}
                <div className="flex items-center gap-3">
                  {gmailConnected ? (
                    <>
                      <div className="flex items-center gap-2 text-sm text-green-600">
                        <div className="h-2 w-2 bg-green-500 rounded-full"></div>
                        Gmail Connected
                      </div>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={handleUnlinkGmailForContacts}
                        disabled={isUnlinkingGmail}
                        className="text-red-600 border-red-600 hover:bg-red-50"
                      >
                        {isUnlinkingGmail ? (
                          <>
                            <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                            Unlinking...
                          </>
                        ) : (
                          <>
                            <X className="h-4 w-4 mr-2" />
                            Unlink Gmail
                          </>
                        )}
                      </Button>
                    </>
                  ) : (
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={handleConnectGmailForContacts}
                      disabled={isConnectingGmail}
                      className="text-blue-600 border-blue-600 hover:bg-blue-50"
                    >
                      {isConnectingGmail ? (
                        <>
                          <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                          Connecting...
                        </>
                      ) : (
                        <>
                          <Mail className="h-4 w-4 mr-2" />
                          Link Gmail
                        </>
                      )}
                    </Button>
                  )}
                </div>
              </div>
            </div>

            {/* To Field */}
            <div>
              <Label htmlFor="to" className="text-sm font-medium text-gray-700 mb-1 block">
                To *
              </Label>
              <Input
                id="to"
                value={emailData.to}
                onChange={(e) => setEmailData(prev => ({ ...prev, to: e.target.value }))}
                placeholder="Enter recipient email"
              />
            </div>

            {/* CC Field */}
            <div>
              <Label htmlFor="cc" className="text-sm font-medium text-gray-700 mb-1 block">
                CC
              </Label>
              <Input
                id="cc"
                value={emailData.cc}
                onChange={(e) => setEmailData(prev => ({ ...prev, cc: e.target.value }))}
                placeholder="Enter CC emails (comma separated)"
              />
            </div>

            {/* BCC Field */}
            <div>
              <Label htmlFor="bcc" className="text-sm font-medium text-gray-700 mb-1 block">
                BCC
              </Label>
              <Input
                id="bcc"
                value={emailData.bcc}
                onChange={(e) => setEmailData(prev => ({ ...prev, bcc: e.target.value }))}
                placeholder="Enter BCC emails (comma separated)"
              />
            </div>

            {/* Subject Field */}
            <div>
              <Label htmlFor="subject" className="text-sm font-medium text-gray-700 mb-1 block">
                Subject *
              </Label>
              <Input
                id="subject"
                value={emailData.subject}
                onChange={(e) => setEmailData(prev => ({ ...prev, subject: e.target.value }))}
                placeholder="Enter email subject"
              />
            </div>

            {/* Body Field */}
            <div>
              <Label htmlFor="body" className="text-sm font-medium text-gray-700 mb-1 block">
                Message *
              </Label>
              <textarea
                id="body"
                value={emailData.body}
                onChange={(e) => setEmailData(prev => ({ ...prev, body: e.target.value }))}
                placeholder="Enter your message..."
                className="w-full min-h-[200px] p-3 border border-gray-300 rounded-md resize-y focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
              />
            </div>

            {/* Attachments Field (Future Enhancement) */}
            <div>
              <Label className="text-sm font-medium text-gray-700 mb-1 block">
                Attachments
              </Label>
              <div className="flex items-center gap-2 p-3 border border-gray-300 rounded-md bg-gray-50">
                <Paperclip className="h-4 w-4 text-gray-400" />
                <span className="text-sm text-gray-500">Attachment support coming soon</span>
              </div>
            </div>
          </div>

          <DialogFooter className="flex justify-between items-center pt-4 border-t">
            <div className="flex items-center gap-3">
              <Button
                variant="outline"
                onClick={() => setShowEmailModal(false)}
                disabled={isSendingEmail}
              >
                Cancel
              </Button>

              {/* Email Service Status */}
              <div className="flex items-center gap-2">
                {checkingEmailServices ? (
                  <div className="flex items-center gap-2 text-sm text-gray-500">
                    <Loader2 className="h-4 w-4 animate-spin" />
                    Checking email services...
                  </div>
                ) : gmailConnected ? (
                  <div className="flex items-center gap-2 text-sm text-green-600">
                    <div className="h-2 w-2 bg-green-500 rounded-full"></div>
                    Ready to send via Gmail
                  </div>
                ) : smtpAvailable ? (
                  <div className="flex items-center gap-2 text-sm text-blue-600">
                    <div className="h-2 w-2 bg-blue-500 rounded-full"></div>
                    Ready to send via SMTP
                  </div>
                ) : (
                  <div className="flex items-center gap-2 text-sm text-gray-500">
                    <AlertCircle className="h-4 w-4" />
                    No email service available
                  </div>
                )}
              </div>
            </div>

            <Button
              onClick={handleSendEmail}
              disabled={isSendingEmail || !emailData.to || !emailData.subject || !emailData.body || (!gmailConnected && !smtpAvailable)}
              className="bg-orange-600 hover:bg-orange-700"
            >
              {isSendingEmail ? (
                <>
                  <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                  Sending...
                </>
              ) : (
                <>
                  <Send className="h-4 w-4 mr-2" />
                  Send Email
                </>
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </Page>
  );
};

export default Lists;
